import {Component, OnInit} from '@angular/core';
import {ROUTER_DIRECTIVES} from '@angular/router';
import {Menu,OPERATIONMENU,ORDERBYMENU,LIMITMENU} from './dropDownLists';
import {AdminHirekHeaderComponent} from "./header.component";
import {APIService} from '../../../globals/services/API.service';
import {ErrorService} from '../../../globals/services/error.service';
import {GridController} from '../../../globals/TYPES';
import {hunkCurrencyPipe} from '../../../globals/pipes/hunkCurrency.pipe';
import {markPipe} from '../../../globals/pipes/mark.pipe';
import {hunkDate} from '../../../globals/pipes/hunkDate.pipe';
import { DomSanitizationService, SafeHtml } from '@angular/platform-browser';
import {MyInnerHtmlComponent} from '../../../components/my-inner-html/my-inner-html.component';

@Component({
    selector: 'ingatlan-lisa',
    moduleId: module.id,
    templateUrl: 'hirekLista.html',
    providers: [APIService],
    styleUrls: ["hirekLista.css"],
    directives: [AdminHirekHeaderComponent,ROUTER_DIRECTIVES,MyInnerHtmlComponent],
    pipes: [hunkCurrencyPipe, markPipe,hunkDate],

})
export class HirekListaComponent implements OnInit {
    operationMenu:Menu;
    rightMenu:Menu;
    orderByMenu:Menu;

    limitMenu:Menu;
    gridController:GridController;
    news:Array<any>;
    avaiblePages:Array<number>;
    lastPage:number;
    searchByText:boolean = false;

    constructor(private _APIService:APIService,
                private _ErrorService:ErrorService,
                private domSanitizationService: DomSanitizationService) {
        this.operationMenu = OPERATIONMENU;

        this.orderByMenu = ORDERBYMENU;
        this.limitMenu = LIMITMENU;
        this.gridController = {
            limit: 50,
            offset: 0,
            orderBy: {
                name: 'Rögzítés időpontja',
                alias: 'id'
            },
            count: -1,
            currentPage: 1,
            PageTurnerShowNumberOfPages: 5,
            searchText: ''
        };
    }


    ngOnInit() {
        this.loadList();
    }

    loadList() {
        if (this.gridController.count < 0) this.getCount();
        let params = '&limit=' + this.gridController.limit + '&offset=' + this.gridController.offset + '&orderBy=' + this.gridController.orderBy.alias;

        let link = 'api/news/' + params;


          console.log(link);


        this._APIService.getResponseGET(link, '')
            .subscribe(
                data=> {
                //console.log(data);
                this.news = data;
                this.setPageTurner();


            },
                error=> {
                console.log(error);
            }
        )
    }

    private  getCount() {
        this._APIService.getResponseGET('api/news/where=id>0&getCount', '')
            .subscribe(
                data=> {
                /*console.log(data);*/
                this.gridController.count = data;

            },
                error=> {
                console.log(error);
            }
        )

    }

    setPageTurner() {
        let pages = this.gridController.count / this.gridController.limit;
        this.lastPage = Math.ceil(pages);
        var avaiblePages = [];
        let midlePoint = Math.ceil(this.gridController.PageTurnerShowNumberOfPages / 2);
        let startNumber = 1;
        if (this.gridController.currentPage > midlePoint) {
            startNumber = this.gridController.currentPage - midlePoint + 1;
        }
        if (this.gridController.currentPage > this.lastPage - midlePoint + 1) {
            startNumber = this.lastPage - this.gridController.PageTurnerShowNumberOfPages + 1;

        }
        for (let i = 1; i < this.lastPage + 1; i++) {

            if (i >= startNumber)
                avaiblePages.push(i);
            if (avaiblePages.length >= this.gridController.PageTurnerShowNumberOfPages) break;
        }
        this.avaiblePages = avaiblePages;

    }

    gotoPage(jump) {
        if (jump < 1) jump = 1;
        if (jump > this.lastPage) jump = this.lastPage;
        this.gridController.currentPage = jump;
        this.setPageTurner();
        this.gridController.offset = (jump - 1) * this.gridController.limit;
        this.loadList();

    }

    menuOpen(menu) {
        console.log(menu);
        menu.visible = !menu.visible;
    }

    changeLimit(limit) {
        this.gridController.currentPage = Math.ceil(this.gridController.offset / limit);

        this.gridController.limit = limit;
        this.loadList();
        this.limitMenu.visible = false;

    }

    changeOrderBy(orderBy) {
        this.gridController.orderBy = orderBy;
        this.orderByMenu.visible = false;
        console.log(this.gridController);
        this.loadList();


    }

    changeOffset(offset) {
        this.gridController.offset = offset;
        this.loadList();
    }

    search(value) {
        this.gridController.searchText = value;
        this.loadList();

    }

    runOperation(operation:string) {
        console.log(operation);

    }

    modify(id) {

        window.location.href = '/#/ingatlanok/modify/' + id;
    }

    delete(id) {
        var txt;
        var r = confirm("Biztosan törlöd az ingatlant?!");
        if (r == true) {
            this._APIService.getResponsePUT('api/new/archive/'+id, '').subscribe(
                    data=> {
                    this.loadList();
                },
                    error=> {
                    console.log(error);
                })
        } else {

        }
    }
    getContent(content): SafeHtml {
        return this.domSanitizationService.bypassSecurityTrustHtml(content);
    }
}





